@import url(https://fonts.googleapis.com/css?family=Yesteryear&display=swap);
@import url(https://fonts.googleapis.com/css?family=Sarina&display=swap);
@import url(https://fonts.googleapis.com/css?family=Dancing+Script&display=swap);
@import url(https://fonts.googleapis.com/css?family=Ceviche+One&display=swap);
.head{
    height: 65px;

    display: flex;
    justify-content: center;
    align-items: center;

    background-color: #24292e;
    color: #ffffff;
}

.head .title{
    font-family: 'Yesteryear', cursive;
    font-size: 36px;
}
.footer{
    padding: 2px;

    font-family: 'Sarina', cursive;
    font-size: 14px;
    text-align: center;
}
.actionSection{
    margin: 0 10px;
    display: flex;
    justify-content: space-between;

    border-bottom: 1px solid #d9d9d9;
}

.actionSection>div{
    padding: 6px 3px;

    font-family: 'Dancing Script', cursive;
    font-size: 16px;
    text-align: left;
    color: #000000;
}

.actionSection>div#number{
    flex-basis: 2%;
}

.actionSection>div#name{
    flex-basis: 50%;
}

.actionSection>div#status, .actionSection>div#actions{
    flex-basis: 20%;
}

.actionSection>div#actions{
    text-align: right;
}
.todo{
    background-color: #ffffff;
    width: 60vw;
    min-height: 75vh;
    margin: 50px auto;
}

.todo h2{
    font-family: 'Ceviche One', cursive;
    font-size: 28px;
    text-align: center;
}

.todo .inputs{
    display: flex;
   justify-content: space-around;
   
   padding: 0 10px;
   padding-bottom: 10px;
}

.todo .inputs .addButton{
    margin-left: 10px;
}

.todo .data{
    min-height: 55vh;
}
#root{
    height: 100vh;
    background-color: #c8cacc;
}

